import React, { Component } from 'react';
import './FormAdd.css';

class FormAdd extends Component {
	constructor(props) {
		super(props);
		this.state = {newItem: '', addDisabled: true};
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
	}

  	handleChange(event) {
  		if (event.target.value !== '') {
  			this.setState({addDisabled: false});
  		}
  		else { 
  			this.setState({addDisabled: true});
  		}
		this.setState({newItem: event.target.value});
	}

	handleSubmit(event) {
		event.preventDefault();
		this.props.addItem(this.state.newItem);
		this.setState({newItem: '', addDisabled: true});
	}

	render() {
		return (
			<form onSubmit={this.handleSubmit}>
				<input type="text" value={this.state.newItem} onChange={this.handleChange} />
				<input type="submit" value="+" disabled={this.state.addDisabled} />
			</form>
		);
	}
}

export default FormAdd;



