import React, { Component } from 'react';
import './App.css';
import Projects from "./Projects";

class App extends Component {
	constructor(props) {
		super(props);

		this.state = {
			version: '1.0'
		}
	}
	render() {
		var message = '';

		if (localStorage.getItem('version') !== this.state.version) {
			localStorage.clear();
			message = "Your data has been cleared with the release of a new version.";
			localStorage.setItem('version', this.state.version);
		}

		return (
			<div className="App">
			<div className="contrast">{message}</div>
			<Projects />
			</div>
			);
		}
	}

export default App;



