import React, { Component } from 'react';
import './ListBox.css';
import FormAdd from './FormAdd';

class ListBox extends Component {
	constructor(props) {
		super(props);

		this.state = {
			'tasks': this.props.project.tasks
		}

		this.addTask = this.addTask.bind(this);
		this.delTask = this.delTask.bind(this);
	}

	addTask(name) {
		this.props.addTask(name, this.props.project.pid);
	}

	delTask(tid) {
		this.props.delTask(tid, this.props.project.pid);
	}

	render() {
		return (
			<div>
				<ul className="list-box">
					{this.state.tasks.map((task, index) =>
						<li key={task.tid} index={index}>{task.name}<input type="submit" value="x" className="remove right" onClick={() => this.delTask(task.tid)} /></li>
					)}
				</ul>
				<FormAdd type="task" newItem={this.state.newItem} addItem={this.addTask} />
			</div>
		);
	}
}

export default ListBox;



