import React, { Component } from 'react';
import './App.css';
import './Projects.css';
import ListBox from "./ListBox";
import FormAdd from './FormAdd';

class Projects extends Component {
	constructor(props) {
        super(props);

        var projects = (localStorage.getItem('projects') && localStorage.getItem('projects').length) ? JSON.parse(localStorage.getItem('projects')) : [];
        var projectsTracker = localStorage.getItem('projectsTracker') && localStorage.getItem('projectsTracker').length ? localStorage.getItem('projectsTracker') : 0;
        var tasksTracker = localStorage.getItem('tasksTracker') && localStorage.getItem('tasksTracker').length ? localStorage.getItem('tasksTracker') : 0;

        this.state = {
            'projects': projects,
            'newProject': '',
            'count': projects.length,
            'pTracker': projectsTracker,
            'tTracker': tasksTracker
        };

        this.addProject = this.addProject.bind(this);
        this.addTask = this.addTask.bind(this);
        this.delProj = this.delProj.bind(this);
        this.delTask = this.delTask.bind(this);
    }

    addProject(name) {
        var cState = this.state;
        cState.projects.push({
            'pid': cState.pTracker++,
            'name': name,
            'tasks': []
        });
        cState.count++;
        this.setState(cState);
        localStorage.setItem('projects', JSON.stringify(cState.projects));
        localStorage.setItem('projectsTracker', JSON.stringify(cState.pTracker));
    }

    addTask(name, pid) {
        for (var i=0; i<this.state.projects.length; i++) {
            if (this.state.projects[i].pid === pid) {
                var cState = this.state;

                cState.projects[i].tasks.push({
                    'tid': this.state.tTracker,
                    'name': name
                });
                cState.tTracker++;
                this.setState(cState);
                localStorage.setItem('projects', JSON.stringify(cState.projects));
                localStorage.setItem('tasksTracker', JSON.stringify(cState.tTracker));
                break;
            }
        }
    }

    delProj(pid) {
        var cState = this.state;

        for (var i=0; i<cState.projects.length; i++) {
            if (cState.projects[i].pid === pid) {
                cState.projects.splice(i, 1);
                break;
            }
        }
        cState.count--;
        this.setState(cState);
        localStorage.setItem('projects', JSON.stringify(cState.projects));
    }

    delTask(tid, pid) {
        var cState = this.state;

        for (var i=0; i<cState.projects.length; i++) {
            if (cState.projects[i].pid === pid) {
                var tasks = cState.projects[i].tasks;
                for (var j=0; j<tasks.length; j++) {
                    if (tasks[j].tid === tid) {
                        cState.projects[i].tasks.splice(j, 1);
                        break;
                    }
                }
                break;
            }
        }
        this.setState(cState);
        localStorage.setItem('projects', JSON.stringify(cState.projects));
    }

    render() {
        var projectTitle = this.state.count < 2 ? this.state.count + " Project" : this.state.count + " Projects";

        return (
            <div>
                <h2>{projectTitle}</h2>
                <div className="add-form">
                    <h5>Add new project</h5>
                    <FormAdd newItem={this.state.newProject} addItem={this.addProject} />
                </div>
                <div>
                    {this.state.projects.map((project, index) =>
                        <div key={project.pid} className="box">
                        <h5>{project.name} <span className="small"> - {project.tasks.length} task(s)</span><input type="submit" value="x" className="remove right" onClick={() => this.delProj(project.pid)} /></h5>
                        <ListBox key={project.pid} index={index} project={project} addTask={this.addTask} delTask={this.delTask} />
                        </div>
                    )}
                </div>
            </div>
        );
    }
}

export default Projects;



